html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

html {
    font-size: 62.5%;
    height: 100vh;
    background: lightgrey;
}

#h1-mod-logs {
  font-size: 4.8rem;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
  margin: 1rem;
}

#logs-heading{
  display: flex;
  margin: 1rem;
}

.logs-types {
  font-family: Raleway, 'Times New Roman', Times, serif;
  font-size: 2rem;
}

.regl-user {
  width: 15%;
  text-align: center;
}

.regl-action {
  width: 15%;
  text-align: center;
}

.regl-id {
  width: 10%;
  text-align: center;
}

.regl-date {
  width: 15%;
  text-align: center;
}

.regl-reason {
  width: 25%;
  text-align: center;
}

.regl-actions {
  width: 20%;
  text-align: center;
}

.log-cards {
  display: flex;
  margin: 1rem;
  border: 3px solid black;
  background: darkgray;
}

.log-data {
  font-size: 2rem;
}

#h3-log-reversed {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
}

.logger-buttons {
  text-decoration: none;
    color: black;
    display: block;
    margin: 0 auto;
    font-size: 1.8rem;
    cursor: pointer;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
}

.logger-buttons:hover {
  color:white;
  background: black;
  transition: 0.25s;
}